
.res-nav{
    width: 100vw;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: fixed;
    right: 0;
    top: 12vh;
    background-color: rgba(8, 31, 40,0.8);
    z-index: 2;
    color: #d2ae68;
    animation: Res 2s;
}
.res-nav a, .res-reverse{
    padding: 4px 16px 8px 16px;
    margin-bottom: 4px;
    text-align: center;
    font-size: 24px;
    transition: 0.2s all ease-in-out;
    border-bottom: 3px solid transparent;
}
.res-nav a:hover{
    border-bottom: 3px solid rgb(170, 111, 111);
}
.res-reverse:hover{
    border-bottom: 3px solid rgb(170, 111, 111);
cursor: pointer;
}
@keyframes Res {
    0%{transform: translateX(100vw);}
    100%{transform: translateY(0);}
}